<template>
  <div class="hello">
    <!-- <h1>{{ msg }}</h1> -->
    <h2>Essential Links</h2>
    <video v-show="videoflg" ref="video" width="100%" autoplay playsinline></video>
    <canvas v-show="canvasflg" ref="canvas" id="canvas"></canvas>
    <img id="image">
    <button id="snap" v-on:click="capture()">Snap Photo</button>
  </div>
</template>

<script>
export default {
  name: 'hello1',
  data () {
    return {
      video: {},
      canvas: {},
      captures: [],
      videoflg: true,
      canvasflg: false
    }
  },
  mounted () {
    this.video = this.$refs.video
    const medias = {audio: false, video: true}
    // if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
    //   navigator.mediaDevices.getUserMedia({ video: true }).then(stream => {
    //     this.video.src = window.URL.createObjectURL(stream)
    //     this.video.play()
    //   })
    // }
    navigator.getUserMedia(medias, this.successCallback, this.errorCallback)
  },
  methods: {
    successCallback (stream) {
      this.video.srcObject = stream
    },
    errorCallback (err) {
      alert(err)
    },
    capture () {
      // alert(navigator.platform)
      this.canvas = this.$refs.canvas
      var w = this.video.offsetWidth
      var h = this.video.offsetHeight
      this.canvas.setAttribute('width', w)
      this.canvas.setAttribute('height', h)
      // var context = this.canvas.getContext('2d').drawImage(this.video, 0, 0, w, h)
      this.$data.canvasflg = true
      this.canvas.getContext('2d').drawImage(this.video, 0, 0, w, h)
      this.$data.videoflg = false
      this.captures.push(this.canvas.toDataURL('image/png'))
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #35495E;
}
</style>



